.diff-view-container {
  position: fixed;
  z-index: 1001;
  background: white;
  border: 1px solid var(--color-gray-200);
  border-radius: 12px;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.15), 0 8px 16px rgba(0, 0, 0, 0.1);
  max-width: 600px;
  min-width: 400px;
  animation: fade-in-up 0.2s ease-out;
  transition: box-shadow 0.2s ease;
}

.diff-view-container.dragging {
  box-shadow: 0 25px 50px rgba(0, 0, 0, 0.25), 0 10px 20px rgba(0, 0, 0, 0.15);
  cursor: grabbing !important;
}

.diff-view-header {
  padding: 14px 16px;
  border-bottom: 1px solid var(--color-gray-200);
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: linear-gradient(to bottom, var(--color-gray-50), white);
  border-radius: 12px 12px 0 0;
  cursor: grab;
  user-select: none;
}

.diff-view-header:active {
  cursor: grabbing;
}

.diff-view-header-content {
  display: flex;
  align-items: center;
  gap: 8px;
}

.diff-view-header-content > div {
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.drag-handle {
  color: #9ca3af;
  font-size: 16px;
  line-height: 1;
  cursor: grab;
  opacity: 0.6;
  transition: opacity 0.2s ease;
}

.diff-view-header:hover .drag-handle {
  opacity: 1;
}

.diff-view-title {
  font-size: 14px;
  font-weight: 700;
  color: #111827;
  letter-spacing: 0.2px;
}

.diff-view-subtitle {
  font-size: 12px;
  color: #6b7280;
}

.diff-close-button {
  background: transparent;
  border: none;
  color: #9ca3af;
  font-size: 18px;
  cursor: pointer;
  padding: 4px 8px;
  border-radius: 6px;
  transition: all 0.15s ease;
  line-height: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  z-index: 1;
}

.diff-close-button:hover {
  background: #f3f4f6;
  color: #374151;
}

.diff-view-content {
  padding: 16px;
  max-height: 400px;
  overflow-y: auto;
  font-family: ui-monospace, SFMono-Regular, 'SF Mono', Menlo, Consolas, 'Liberation Mono', monospace;
  font-size: 13px;
  line-height: 1.7;
  white-space: pre-wrap;
  word-wrap: break-word;
  background: var(--color-gray-50);
}

.diff-view-content.scrollbar-thin {
  /* Scrollbar styles are now inherited from shared styles */
}

.diff-unchanged {
  color: #374151;
}

.diff-removed {
  background-color: #fee2e2;
  color: #991b1b;
  text-decoration: line-through;
  padding: 2px 0;
  border-radius: 3px;
}

.diff-added {
  background-color: #dcfce7;
  color: #166534;
  padding: 2px 0;
  border-radius: 3px;
  font-weight: 500;
}

.diff-view-actions {
  padding: 12px 16px;
  display: flex;
  gap: 10px;
  border-top: 1px solid var(--color-gray-200);
  background: white;
  border-radius: 0 0 12px 12px;
}

.diff-action-button {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  padding: 10px 16px;
  border: 1px solid var(--color-gray-200);
  border-radius: 12px;
  background: white;
  cursor: pointer;
  transition: all 0.2s ease;
  font-weight: 600;
  font-size: 14px;
}

.diff-action-button:hover {
  transform: translateY(-1px);
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.diff-action-button.reject {
  color: #dc2626;
  border-color: #fca5a5;
}

.diff-action-button.reject:hover {
  background: #fef2f2;
  border-color: #dc2626;
}

.diff-action-button.accept {
  color: #16a34a;
  border-color: #86efac;
  background: #f0fdf4;
}

.diff-action-button.accept:hover {
  background: #dcfce7;
  border-color: #16a34a;
}

.action-icon {
  font-size: 16px;
  font-weight: bold;
}

.action-label {
  letter-spacing: 0.3px;
}

.streaming-text {
  color: #374151;
  font-family: ui-monospace, SFMono-Regular, 'SF Mono', Menlo, Consolas, 'Liberation Mono', monospace;
  font-size: 13px;
  line-height: 1.7;
  white-space: pre-wrap;
  word-wrap: break-word;
}

.streaming-cursor {
  display: inline-block;
  animation: cursor-blink 1s step-end infinite;
  color: var(--color-blue);
  font-weight: bold;
}

