.App {
  text-align: center;
}

/* Main container */
.app-container {
  font-family: ui-sans-serif, system-ui, -apple-system, Segoe UI, Roboto, Helvetica, Arial;
  position: relative;
  min-height: 100vh;
}

/* Content wrapper */
.content-wrapper {
  max-width: 700px;
  margin: 40px auto;
  padding: 0 16px;
  /* padding-left: 240px;  */
}

/* Header section */
.header-section {
  margin-bottom: 16px;
}

.header-title {
  font-size: 28px;
  font-weight: 700;
  margin-bottom: 8px;
}

.header-description {
  font-size: 14px;
  color: #6b7280;
  margin-bottom: 12px;
}

/* Contract selector */
.contract-selector-container {
  display: flex;
  align-items: center;
  gap: 12px;
  margin-bottom: 12px;
}

.selector-label {
  font-size: 14px;
  font-weight: 500;
  color: #374151;
}

.contract-select {
  padding: 8px 12px;
  border-radius: 8px;
  border: 1px solid #d1d5db;
  font-size: 14px;
  font-weight: 500;
  background-color: white;
  color: #111827;
  cursor: pointer;
  outline: none;
  min-width: 200px;
}

/* Role selector */
.role-selector-container {
  display: flex;
  align-items: center;
  gap: 12px;
}

.role-button-group {
  display: inline-flex;
  background-color: #f3f4f6;
  border-radius: 8px;
  padding: 4px;
  gap: 4px;
}

.role-button {
  padding: 8px 16px;
  border-radius: 6px;
  border: none;
  font-size: 14px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s;
}

.role-button.active {
  background-color: white;
  color: #111827;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.05);
}

.role-button:not(.active) {
  background-color: transparent;
  color: #6b7280;
  box-shadow: none;
}

/* Editor layout */
.editor-layout {
  display: flex;
  gap: 20px;
  align-items: flex-start;
  width: 100%;
}

.editor-wrapper {
  flex: 0 0 auto;
  width: 100%;
  max-width: 700px;
  border-radius: 12px;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
}


/* Editor content styles */
.ProseMirror {
  min-height: 200px;
  max-height: 70vh;
  padding: 16px;
  border: 1px solid #e5e7eb;
  border-radius: 12px;
  line-height: 1.6;
  outline: none;
  font-size: 14px;
  background: white;
  word-wrap: break-word;
  overflow-wrap: break-word;
  overflow-x: hidden;
  overflow-y: auto;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', sans-serif;
}

/* Startup animation highlight for selected text */
.ProseMirror p:first-child::selection {
  background-color: rgba(var(--color-blue-rgb), 0.3);
  animation: text-highlight 2s ease-out;
}

.ProseMirror p:first-child::-moz-selection {
  background-color: rgba(var(--color-blue-rgb), 0.3);
  animation: text-highlight 2s ease-out;
}

.text-selection-popup.startup-animation {
  animation: pulse-ring 2s ease-out;
}
